import React from 'react';
import { useSpring, animated } from "react-spring";

const Sort = ({ handleSort, sort }) => {

    const fadeSort = useSpring({
        config: { duration: 500 },
        from: { left: -100, opacity: 0 }, to: { opacity: 1, left: 0 }
    })
    return (
        <animated.section className="section" style={fadeSort}>
            <header>
                <h1 className="header-sort">Sort</h1>
            </header>
            <div className="sort" onChange={handleSort}>
                <label> Low To High<input className="sort-input" type="radio" value="Low to High" name="sort" /></label>
                <label> High to Low<input className="sort-input" type="radio" value="High to Low" name="sort" /></label>
                <label> Default<input className="sort-input" type="radio" value="Default" name="sort" /></label>
                <button onClick={sort}>Sort</button>
            </div>
        </animated.section>
    );
}

export default Sort;