import React from "react";
import { useSpring, animated } from "react-spring";

const Filter = ({ handleFiltersLowest, handleFiltersHighest, handleBrandChecked, handleBrandChecked2, handleBrandChecked1, filters,
    labelUp, labelUpState, labelUpHighState, labelUpHighPrice, handleSearch, showSearchInput, searchShow }) => {
    const fadeFilters = useSpring({
        config: { duration: 500 },
        from: { right: -100, opacity: 0 }, to: { opacity: 1, right: 0 }, delay: 250
    })
    const labelHighPriceAnimate = useSpring({
        config: { duration: 500 },
        top: labelUpHighState ? -15 : 0
    })
    const labelLowPriceAnimate = useSpring({
        config: { duration: 500 },
        top: labelUpState ? -15 : 0
    })
    window.onclick = () => { labelUpHighPrice(); labelUp(); }
    return (
        <animated.section className="section" style={fadeFilters}>
            <header>
                <h1 className="filters-header">Filters</h1>
            </header>
            <div className="price-filtr">
                <div className="filter-field">
                    <label className="low-price-label label-style" >
                        <animated.span style={labelLowPriceAnimate}>Lowest price</animated.span>
                        <input className="low-price inputs " type="number" onChange={handleFiltersLowest} onClick={labelUp} />
                        <span className="field-underline"></span>
                    </label>
                    <label className="high-price-label label-style">
                        <animated.span style={labelHighPriceAnimate}>Higher price</animated.span>
                        <input className="high-price inputs " type="number" onChange={handleFiltersHighest} onClick={labelUpHighPrice} />
                        <span className="field-underline"></span>
                    </label>
                    <div className="search-text">
                        <label htmlFor="search" className="filter-field-search">
                            <input type="text" id="search" placeholder="Search" onChange={handleSearch} className={searchShow ? "search-input-active search-input" : "search-input"} />
                        </label>
                        <div className="search-icon" onClick={showSearchInput}>
                            <i className="fas fa-search"></i>
                        </div>
                    </div>
                </div>
                <div className="filters" > <strong>Brands:</strong>
                    <label> AMD <input className="brand-type" type="checkbox" name="brand" value="AMD" onChange={handleBrandChecked} /></label>
                    <label> Intel<input className="brand-type" type="checkbox" name="brand" value="INTEL" onChange={handleBrandChecked1} /></label>
                    <label> Nvidia <input className="brand-type" type="checkbox" name="brand" value="NVIDIA" onChange={handleBrandChecked2} /></label>
                </div>
                <button onClick={filters}>Filter</button>
            </div>
        </animated.section>
    );
}

export default Filter;