import React from 'react';
import { NavLink } from "react-router-dom";
import logo from "./img/DEVadrian.png";
import { useSpring, animated } from "react-spring";
import FlipMove from "react-flip-move";

const Nav = ({ isScroll, clickCart, showCart, cancelOrder, uniqueCart, isMaxCart }) => {
    const cartIconAnimate = useSpring({
        canfig: { duration: 750 },
        from: { opacity: 0, top: 50 }, to: { opacity: 1, top: 0 }
    })

    const cartIconTriggered = useSpring({
        config: { duration: 750 },
        top: clickCart ? -10 : 0
    })

    const navSmoothAnimated = useSpring({
        config: { duration: 500 },
        position: isScroll ? "fixed" : "static",
        top: isScroll ? 0 : -200,

    })

    const CartNumberProducts = () => {
        if (cartProducts > 0) {
            return (
                <span className="count-products">{cartProducts}</span>
            )
        } else return null;
    }
    let cartProducts = uniqueCart.length;
    let height = 175;

    const heightChange = () => {
        height = height * (cartProducts + 1);
    }
    heightChange();
    const fadeCart = useSpring({
        config: { duration: 600 },
        opacity: clickCart ? 1 : 0,
        height: clickCart ? height : 0
    })
    const fadeCartItem = useSpring({
        config: { duration: 400 },
        opacity: clickCart ? 1 : 0, delay: 500
    })
    const isMaxCartFunction = useSpring({
        config: { duration: 700 },
        opacity: isMaxCart ? 1 : 0, top: isMaxCart ? 0 : -50,
    })
    const emptyWordAnimate = useSpring({
        config: { duration: 400 },
        opacity: clickCart ? 1 : 0, delay: 400
    })
    const MoreProducts = () => {
        if (cartProducts > 2) {
            return (
                <animated.div className="more-products" style={isMaxCartFunction}>
                    <p>{`${cartProducts} is max product`}</p>
                </animated.div>
            );
        } else if (cartProducts === 0) {
            return (
                <animated.strong style={emptyWordAnimate} className="empty-cart">
                    Your Cart Is  Empty! Let's start Shoping!
                </animated.strong>
            );
        } else return null;
    }

    let totalPrice = 0;

    const ShowTotal = () => {
        if (cartProducts > 0) {
            return (
                <div className="total-price">
                    Total To Pay:
                <strong className="total">
                        {totalPrice}zł
                    </strong>
                </div>
            )
        } else return null
    }

    return (
        <animated.nav className={isScroll ? "nav nav-fixed " : "nav"} style={navSmoothAnimated}>
            <a href="https://www.facebook.com/adrian.raszka.731" target="blank"><div ><img className="logo" src={logo} alt="logo" /></div></a>
            <ul className="nav-bar">
                <NavLink activeStyle={{ fontSize: "21px", color: "orange" }} exact to="/" >
                    <li className="item">
                        Products
                    </li>
                </NavLink>
                <NavLink exact to="/info" activeStyle={{ fontSize: "21px", color: "orange" }}>
                    <li className="item">
                        Info
                    </li>
                </NavLink>
                <NavLink exact to="/contact" activeStyle={{ fontSize: "21px", color: "orange" }}>
                    <li className="item">
                        Contact
                    </li>
                </NavLink>
                <div className="cart-conteiner">
                    <animated.i className="fas fa-shopping-cart " style={cartIconAnimate, cartIconTriggered} onClick={showCart}>
                        <CartNumberProducts />
                    </animated.i>
                    <animated.div style={fadeCart} className={clickCart ? "cart-info" : "cart-info-inactive ", isScroll ? "cart-info cart-info-scroll" : "cart-info"}>
                        <FlipMove duration={700} easing="ease-in-out">
                            {uniqueCart.map((c) => {
                                let increase = () => {
                                    if (c.units <= 5) {
                                        c.units++
                                    }
                                }
                                let decrease = () => {
                                    if (c.units > 1) {
                                        c.units--
                                    }
                                }
                                let total = () => {
                                    totalPrice += c.price * c.units;
                                    ShowTotal();
                                }
                                total();
                                return (
                                    <animated.div style={fadeCartItem} className="cart" key={c.id}  >
                                        <h2 className="brand-cart">{c.brand}</h2>
                                        <div className="img"><img src={logo} alt="cart-img" /></div>
                                        <div className="pay-info">
                                            <div className="price-cart">To Pay:{c.price * c.units}zł</div>
                                            <div className="units-cart">Units:{c.units}</div>
                                            <div className="quantity-update-buttons" onClick={increase}
                                            ><span>+</span>
                                            </div>
                                            <div className="quantity-update-buttons" onClick={decrease}>
                                                <span>-</span>
                                            </div>
                                        </div>
                                        <button className="cancel-order" onClick={cancelOrder.bind(this, c.id)}>Cancel</button>
                                    </animated.div>
                                )
                            }
                            )}
                        </FlipMove>
                        <ShowTotal />
                        <MoreProducts />
                    </animated.div>
                </div >
            </ul>
        </animated.nav>
    );
}

export default Nav;